import styles from "../styles/Account.module.scss"

import logo from "../Images/login/logo.svg"
import image from "../Images/account/image.jpg"
import steam from "../Images/account/steam.svg"
import lines from "../Images/bgLines.png"
import burger from "../Images/burger.png"
import tick from "../Images/jobs/ticked.svg"
import photo from "../Images/login/image.jpg"

import { NavLink } from "react-router-dom"
import { useState } from "react"

function Account() {
  const [showBurger, setShowBurger] = useState(false)

  return (
    <div className={styles.login}>
      <img
        src={lines}
        className={styles.lines}
      />
      <div className={styles.left}>
        <div className={styles.top}>
          <img src={logo} />
          <NavLink to='/sunrise/'>Главная</NavLink>
          <NavLink to='/sunrise/contacts'>Контакты</NavLink>
          <button
            className={styles.burgerMenuButton}
            onClick={() => setShowBurger((prev) => !prev)}
          >
            <img src={burger}></img>
          </button>
          {showBurger && (
            <>
              <div
                className={styles.burgerMenuWrapper}
                onClick={() => setShowBurger(false)}
              ></div>
              <div className={styles.burgerMenu}>
                <nav>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/project'
                  >
                    О проекте
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/team'
                  >
                    Руководство
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/jobs'
                  >
                    Работа у нас
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/events'
                  >
                    Мероприятия
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/documents'
                  >
                    Документы
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/contacts'
                  >
                    Контакты
                  </NavLink>
                  <NavLink
                    className={styles.link}
                    to='/sunrise/login'
                  >
                    Личный кабинет
                  </NavLink>
                </nav>
              </div>
            </>
          )}
        </div>
        <div className={styles.adaptive}>
          <img src={photo} />
        </div>
        <div className={styles.main}>
          <h5>Попробуйте бесплатно</h5>
          <h3>Создайте новый аккаунт</h3>
          <p>
            Уже зарегистрированы <NavLink to='/sunrise/login'>Войти</NavLink>
          </p>
          <form>
            <div className={styles.name}>
              <div className={styles.block}>
                <label for='fname'>Ваше имя</label>
                <input
                  type='text'
                  id='fname'
                  placeholder='Игорь'
                ></input>
              </div>
              <div className={styles.block}>
                <label for='sname'>Ваша фамилия</label>
                <input
                  type='text'
                  id='sname'
                  placeholder='Иванов'
                ></input>
              </div>
            </div>
            <div className={styles.block}>
              <label for='mail'>Ваш адрес электронной почты</label>
              <input
                type='mail'
                id='mail'
                placeholder='example@gmail.com'
              ></input>
            </div>
            <div className={styles.block}>
              <label for='login'>Логин</label>
              <input
                type='login'
                id='login'
                placeholder='username123'
              ></input>
            </div>
            <div className={styles.block}>
              <label for='password'>Пароль</label>
              <input
                type='password'
                id='password'
                placeholder='password_example'
              ></input>
            </div>
            <div className={styles.check}>
              <label for='check'>
                <input
                  type='checkbox'
                  id='check'
                ></input>
                <div className={styles.tick}>
                  <img src={tick} />
                </div>
                <p>
                  Я соглашаюсь с <a href=''>политикой конфиденциальности</a>
                </p>
              </label>
            </div>
          </form>
          <NavLink to='/sunrise/dashboard'>
            <button>Создать аккаунт</button>
          </NavLink>
          <button className={styles.steam}>
            <p>Войти через steam</p>
            <img src={steam} />
          </button>
        </div>
      </div>
      <div className={styles.right}>
        <img src={image} />
      </div>
    </div>
  )
}

export default Account
