import { useState } from "react"

import arrow from "../Images/jobs/Arrow.svg"
import arrow2 from "../Images/jobs/Arrow 2.svg"
import close from "../Images/jobs/close.svg"
import tick from "../Images/jobs/ticked.svg"

import styles from "../styles/Vacation.module.scss"

function Vacation(props) {
  const { id, title, text, extra } = props

  const [activity, setActivity] = useState(0)

  const [modal, setModal] = useState(false)

  const openModal = () => {
    setModal(true)
    document.body.style.overflow = "hidden"
  }

  const closeModal = () => {
    setModal(false)
    document.body.style.overflow = "visible"
  }

  return (
    <>
      <div className={modal === true ? styles.black : styles.noBlack}></div>
      <div className={modal === true ? styles.popup : styles.inActive}>
        <div className={styles.wrapper}>
          <div className={styles.top}>
            <h1>Вакансия</h1>
            <img
              src={close}
              onClick={closeModal}
            />
          </div>
          <div className={styles.form}>
            <p>Заполните простую форму, и мы свяжемся с вами!</p>
            <form>
              <div className={styles.element}>
                <label for='name'>Ваше имя</label>
                <input
                  id='name'
                  type='text'
                  placeholder='Иван'
                ></input>
              </div>
              <div className={styles.element}>
                <label for='age'>Возраст</label>
                <input
                  id='age'
                  type='text'
                  placeholder='22 года'
                ></input>
              </div>
              <div className={styles.element}>
                <label for='call'>Как с вами связаться?</label>
                <textarea
                  id='call'
                  type='text'
                  placeholder='@primer_syda_pisat — Телеграм'
                  rows='1'
                ></textarea>
              </div>
              <div className={styles.element}>
                <label for='reason'>Почему решили откликнуться?</label>
                <textarea
                  rows='2'
                  id='reason'
                  type='text'
                  placeholder='Опыт работы, желание развиваться, помогать проекту'
                ></textarea>
              </div>
              <button type='submit'>Откликнуться</button>
              <div className={styles.bottom}>
                <label for={id}>
                  <input
                    id={id}
                    type='checkbox'
                  ></input>
                  <div className={styles.tick}>
                    <img src={tick} />
                  </div>
                  <p>
                    Я соглашаюсь с <a href=''>политикой конфиденциальности</a>
                  </p>
                </label>
              </div>
            </form>
          </div>
        </div>
      </div>
      <div className={styles.all}>
        <div className={styles.vacation}>
          <div className={styles.left}>
            <h5>{title}</h5>
            <p>{text}</p>
            <div className={styles.extra}>
              {activity === 0 && (
                <div className={styles.more}>
                  <span onClick={() => setActivity(1)}>Узнать больше...</span>
                  <img src={arrow} />
                </div>
              )}
              {activity === 1 && (
                <div className={styles.description}>
                  {extra}
                  <div className={styles.info}>
                    <span onClick={() => setActivity(0)}>Закрыть</span>
                    <img
                      src={arrow2}
                      className={styles.arrow2}
                    />
                  </div>
                </div>
              )}
            </div>
          </div>
          <button onClick={openModal}>Откликнуться</button>
        </div>
      </div>
    </>
  )
}

export default Vacation
