import { NavLink } from "react-router-dom"

import styles from "../styles/Header.module.scss"

import logo from "../Images/logo.png"
import account from "../Images/icon.png"
import burger from "../Images/burger.png"
import { useState } from "react"

function Header() {
  const [showBurger, setShowBurger] = useState(false)
  return (
    <div className={styles.header}>
      <NavLink
        className=''
        to='/sunrise'
      >
        <img src={logo}></img>
      </NavLink>
      <nav className={styles.nav}>
        <NavLink
          className={styles.link}
          to='/sunrise/project'
        >
          О проекте
        </NavLink>
        <NavLink
          className={styles.link}
          to='/sunrise/team'
        >
          Руководство
        </NavLink>
        <NavLink
          className={styles.link}
          to='/sunrise/jobs'
        >
          Работа у нас
        </NavLink>
        <NavLink
          className={styles.link}
          to='/sunrise/events'
        >
          Мероприятия
        </NavLink>
        <NavLink
          className={styles.link}
          to='/sunrise/documents'
        >
          Документы
        </NavLink>
        <NavLink
          className={styles.link}
          to='/sunrise/contacts'
        >
          Контакты
        </NavLink>
        <NavLink
          className={styles.icon}
          to='/sunrise/login'
        >
          <img src={account}></img>
        </NavLink>
      </nav>
      <button
        className={styles.burgerMenuButton}
        onClick={() => setShowBurger((prev) => !prev)}
      >
        <img src={burger}></img>
      </button>
      {showBurger && (
        <>
          <div
            className={styles.burgerMenuWrapper}
            onClick={() => setShowBurger(false)}
          ></div>
          <div className={styles.burgerMenu}>
            <nav>
              <NavLink
                className={styles.link}
                to='/sunrise/project'
                onClick={() => setShowBurger(false)}
              >
                О проекте
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/team'
                onClick={() => setShowBurger(false)}
              >
                Руководство
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/jobs'
                onClick={() => setShowBurger(false)}
              >
                Работа у нас
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/events'
                onClick={() => setShowBurger(false)}
              >
                Мероприятия
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/documents'
                onClick={() => setShowBurger(false)}
              >
                Документы
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/contacts'
                onClick={() => setShowBurger(false)}
              >
                Контакты
              </NavLink>
              <NavLink
                className={styles.link}
                to='/sunrise/login'
                onClick={() => setShowBurger(false)}
              >
                Личный кабинет
              </NavLink>
            </nav>
          </div>
        </>
      )}
    </div>
  )
}

export default Header
